* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: 'Inter', sans-serif;
}

body {
  margin: 0;
  width: 550px;
  margin: auto;
  min-width: 550px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.card--nav{
  background-color: #F55A5A;
  height: 55px;
  width: 550px;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 20px 0 20px 0;
}

.card{
  display: flex;
  align-items: center;
  padding: 18px 40px 18px 40px;
}
.card--img{
  width: 126px;
  height: 168px;
  border-radius: 5px;
  object-fit: cover;
}
.card--content{
  margin-left: 19px;
}
.card--dates{
 
  font-style: normal;
  font-weight: 700;
  font-size: 11px;
  line-height: 12px;
  padding-top: 7px;
}
.card--text{
  font-weight: 400;
  font-size: 11px;
  line-height: 150%;
  padding-top: 9px;
  
}
.card--link{
  color: #918E9B;
  font-size: 11px;
  font-weight: 400;
}
.card--city{
  font-size: 25px;
  font-weight: 700px;
  line-height: 30px;
  height: 40px;

  
}
.card--location{
   font-size: 11px;
  font-weight: 400;
}

hr{
  width: 478px;
  margin: 0 40px;
  border: 1px solid #F5F5F5;

  
}